<template>
  <div class="mobile-container">
    <Header/>

    <div class="m-content">
        <div class="m-pos-bg">
          <img src="@/assets/m/bg.png" alt="" class="bg-img">
          <div class="m-pos-con">
            <div class="pos-con-img" v-if="nodeData.name === ''"><img src="@/assets/img/exchange2.png" alt=""> {{ $t('lang.dpos_node_detail_title') }} </div>
            <div class="pos-con-img" v-else><img src="@/assets/img/list.png" alt=""> {{ nodeData.name}} </div>
            <div class="m-nodeid">{{ nodeid }}</div>
          </div>
        </div>
<!--        <div class="m-head">-->
<!--            <div class="m-item">-->
<!--                <div class="item-lebel">{{ nodeData.total_node}}</div>-->
<!--                <p>Blocks</p>-->
<!--            </div>-->
<!--            <div class="m-item">-->
<!--                <div class="item-lebel">{{ nodeData.total_user}}</div>-->
<!--                <p>Amount</p>-->
<!--            </div>-->
<!--            <div class="m-item">-->
<!--                <div class="item-lebel">{{ nodeData.total_amount}}</div>-->
<!--                <p>Total Dividend</p>-->
<!--            </div>-->
<!--            <div class="m-item">-->
<!--                <div class="item-lebel">{{ nodeData.total_divident }}</div>-->
<!--                <p>Last Time</p>-->
<!--            </div>-->
<!--        </div>-->
        <div class="m-tips">
          {{ $t('lang.dpos_node_detail1') }}
        </div>
       <el-table
            v-loading="loading"
            :data="tableData"
            style="">
            <el-table-column
              prop="address"
              :label="$t('lang.dpos_node_detail2')">
            </el-table-column>
            <el-table-column
              prop="entrust_amount"
              :label="$t('lang.dpos_node_detail3')"
              width="130">
            </el-table-column>
            <el-table-column
              prop="incoming"
              :label="$t('lang.dpos_node_detail4')">
            </el-table-column>
          </el-table>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '@/components/MHeader'
import Footer from '@/components/MFooter'
import pageSize from '@/components/MPage'
import dateFns from 'date-fns'
export default {
    components: {
        Header,
        Footer,
        pageSize
    },
    data() {
            return {
              loading:true,
                tableData: [{}],
                nodeData: [],
                nodeid: ''
            }
        },
        created() {
            this.nodeid = this.$route.query.nodeid;
        },
        computed: {},
        mounted() {
            this.getNodeUserList(this.nodeid)
        },
        methods: {
            getNodeUserList(nodeid){
                this.$http.get("api/node/userlist", {nodeid: nodeid}).then(res => {
                  this.loading = false
                    if (res.data.code === 200){
                        this.tableData = res.data.data.list
                        this.nodeData = res.data.data.node
                    }
                })
            }
        }
}


</script>


<style scoped>
  .m-pos-bg{
    text-align: left;
    position: relative;
    margin-bottom: .8rem;
  }
  .m-pos-bg .bg-img{
    width: 100%;
    height: 8rem;
  }
  .m-pos-con{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    color: #ffffff;
  }
  .pos-con-img{
    margin: 1rem;
    display: flex;
    align-items: center;
  }
  .pos-con-img img{
    width: 2rem;
    height: 2rem;
    margin-right: .8rem;
  }
  .m-nodeid{
    word-wrap:break-word;
    margin: 0 5rem 0 3rem;
  }
    .m-content{
        padding: 1rem;
        background: #E6E9EE;
    }
    .m-head{
        background: #ffffff;
    }
    .m-head .m-item{
        text-align: center;
        border-bottom: 1px solid #F0F0F0;
        padding: 1rem 0;
    }
    .m-item p{
        color: #888888;
        font-size: 12px;
    }
    .item-lebel{
        font-size: 17px;
        margin-bottom: .5rem;
    }
    .m-tips{
        padding: .7rem;
        text-align: left;
    }
</style>



